import React, { Component } from 'react';
import { connect } from 'react-redux';

import { addItem, delItem, clearAll } from '../../actions';
import './App.css';

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      text: '' 
    }
  }

  enterClick = (e) => {
    if(e.key === 'Enter'){
      this.addReminder();
    }
  }

  addReminder = () => {
    const inp = this.refs.input;
    this.props.addItem(this.state.text);
    this.setState({text: ''});
    inp.value = '';
    inp.focus();
  }

  delReminder = (id) => {
    this.props.delItem(id);
  }

  clearReminder = () => {
    this.props.clearAll();
  }

  renderRemindes = () => {
    const { reminders } = this.props;
    return (
      <ul className="list-group col-sm-4">
      {
        reminders.map( (reminder, index)  => {
          return (
            <li 
              key={reminder.id} 
              className='list-group-item'>
              <div className="list-item">
                <div>
                <span>{index+1}. </span>
                {reminder.text}
                </div>
                
                <span 
                  onClick={ () => this.delReminder(reminder.id)}
                  className="list-item delete-button">
                  &#x2715;
                  </span>
              </div>
            </li>
          )
        })
      }
      </ul>
    )
  }

  render() {
    return (
      <div className="app container">
        
        <h2 className="app__title">To-do list</h2>

        <div className="form-inline">
          <div className="form-group">
            <input 
              ref='input'
              className="form-control"
              placeholder="add item"
              onKeyPress={ this.enterClick }
              onChange={e => this.setState({text: e.target.value})}
            />
          </div>
          <button 
            onClick={this.addReminder}
            disabled={this.state.text.length > 0 ? '': true}
            className="btn btn-success">
            add item
          </button>
          <button 
            onClick={this.clearReminder}
            className="btn btn-danger">
            remove all
          </button>
        </div>
        {this.renderRemindes()}
      </div>
    );
  }
}


function mapStateToProps(state) {
  return {
    reminders: state
  }
}

export default connect(mapStateToProps, { addItem, delItem, clearAll }) (App);



