import { ADD_ITEM, DEL_ITEM, CLEAR_ALL } from '../constants';


export const addItem = (text) => {
  const action = {
    type: ADD_ITEM,
    text
  }
  console.log( 'addItem', action);
  return action;
}

export const delItem = (id) => {
  const action = {
    type: DEL_ITEM,
    id
  }
  console.log( 'delete action', action);
  return action;
}

export const clearAll = () => {
  return {
    type: CLEAR_ALL
  }
}


